/* FactBox.css */

.fact-box {
  border: 1px solid #fffffffc;
  padding: 10px;
  margin-bottom: 10px;
  cursor: pointer;
  background-color: transparent;
  transition: background-color 0.3s;
  width: 100%;
  box-sizing: border-box;
}

.fact-box.right {
  border: 1px solid black;
  color: black;
}

.fact-box.left.selected {
  background-color: #fff;
  color: #000;
}