.select-facts {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
    max-height: 400px; /* Adjust the height as needed */
    overflow-y: auto;
  }

  .facts-list {
    overflow-y: auto;
    flex-grow: 1;
    width: 100%; /* Ensures the list takes up the full width */
    margin-bottom: 20px;
  }
  
  .load-more {
    margin-top: 20px;
    padding: 10px 20px;
    cursor: pointer;
    position: absolute;
    bottom: 20px;  
    background-color: #007bff; /* Example button color */
    color: white; /* Button text color */
    border: none;
    border-radius: 5px;
    transition: background-color 0.3s;
    /* Additional styling for hover state */
    &:hover {
      background-color: #0056b3;
    }
  
    /* Styling for disabled state */
    &:disabled {
      background-color: #cccccc;
      cursor: default;
    }
  }


  .timer-counter-wrapper {
    display: flex;
    justify-content: space-between;
    width: 100%;
    margin-bottom: 20px;
  }