/* LeftContainer.css */
.left-container {
    background-color: transparent; /* Invisible background */
    position: absolute;
    width: 30%; /* Adjust the width as needed */
    height: 70%; /* Adjust based on your layout */
    top: 25%; /* Adjust to position below the header */
    padding: 20px;
    box-sizing: border-box;
    left: 12%; /* Adjust based on your layout */
    border-radius: 5px;
    border: 2px solid white;
    overflow-y: auto; /* Enable vertical scrolling */
  }
  
  .news-content {
    height: 100%; /* Take full height of the container */
    overflow-y: auto; /* Enable vertical scrolling */
    padding-right: 10px; /* Optional: Adjust based on your layout */
  }