/* IntroduceEvent.css */

.introduce-event {
  max-width: 600px;
  margin: 0 auto;
  padding: 20px;
  overflow: hidden; /* Clear the float */
}

.introduce-event img {
  float: left; /* Float the image to the left */
  margin-right: 20px; /* Add some space between the image and the text */
  max-width: 40%; /* Adjust the width of the image */
  height: auto;
}

.introduce-event h1 {
  clear: both; /* Ensure the headline doesn't wrap around the floated image */
  word-wrap: break-word;
  font-size: 18px;
  margin-top: 0; /* Adjust the margin to bring the headline closer to the top */
}

.introduce-event p {
  text-align: justify;
  font-size: 15px;
}

/* After the floating elements, add a clearfix to ensure container encloses the floated elements */
.introduce-event::after {
  content: "";
  display: table;
  clear: both;
}

